import axios from "axios";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { PRODUCT } from "../api/constants";
import FileUpload from "../components/fileUpload/FileUpload";

const AdminPage = () => {
  const [getImgurl, setGetImgurl] = useState();

  const onSubmit = async (data) => {
    await new Promise((r) => setTimeout(r, 1000));
    console.log(JSON.stringify(data));
    if(getImgurl == null) {
      alert(' ')
    }
    axios
      .post(`${PRODUCT}`, {
        modelName: data["modelName"],
        brand: data["brand"],
        price: data["price"],
        size: data["size"],
        img: getImgurl
      })
      .then(function (response) {
        // console.log("dta", response.data);
      })
      .catch(function (error) {
        //  
      })
      .then(function () {
        //  
      });
  };

  const getImgUrl = (url) => {
    setGetImgurl(url);
    console.log("rr", url);
  };

  const {
    register,
    handleSubmit,
    formState: { isSubmitting, isDirty, errors },
  } = useForm();
  return (
    <div className="page">
      <h3></h3>
      <div>
        <form onSubmit={handleSubmit(onSubmit)}>
          <label htmlFor="brand"></label>
          <input
            id="brand"
            type="text"
            placeholder="brand"
            aria-invalid={
              !isDirty ? undefined : errors.brand ? "true" : "false"
            }
            {...register("brand", {
              required: "  .",
            })}
          />

          <label htmlFor="modelName"></label>
          <input
            id="modelName"
            type="text"
            placeholder="modelName"
            aria-invalid={
              !isDirty ? undefined : errors.modelName ? "true" : "false"
            }
            {...register("modelName", {
              required: "  .",
            })}
          />

          <label htmlFor="price"></label>
          <input
            id="price"
            type="number"
            placeholder="price"
            aria-invalid={
              !isDirty ? undefined : errors.price ? "true" : "false"
            }
            {...register("price", {
              required: " ",
            })}
          />

          <label htmlFor="size"></label>
          <input
            id="size"
            type="string"
            placeholder="size"
            aria-invalid={!isDirty ? undefined : errors.size ? "true" : "false"}
            {...register("size", {
              required: " ",
            })}
          />

          <FileUpload getImgUrl={getImgUrl} />
          <label htmlFor="type"></label>
          <button type="submit" disabled={isSubmitting}>
            
          </button>
        </form>
        {/*
          
       */}

        {/*
       <FileUpload2 />
       */}
      </div>
    </div>
  );
};

export default AdminPage;
